import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { Link } from '@vkontakte/vkui';
import './Main.css';

const osname = platform();

const Rules = props => (
  <Panel id={props.id} theme="white">
    <PanelHeader
      noShadow
      addon={<HeaderButton onClick={props.go} data-to="home"></HeaderButton>}
      left={<HeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      
    </PanelHeader>
    <Div className="Rules">
      <ol>
       <li>    20.  ,       .     <nobr>78</nobr>.    .</li>
       <li>          Android iOS.</li>
       <li>      ,       .      .</li>
       <li>     : <strong>#BingoNewYearStreamers</strong>.</li>
       <li>     ,        .</li>
       <li>  =  .    ,     .</li>
       <li>    .    .</li>
       <li>     .</li>
       <li>   20 ,   13 .</li>
       <li>   , ,  <Link href="https://vk.com/authors"> </Link>.</li>
      </ol>
    </Div>
  </Panel>
);

Rules.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};

export default Rules;



